import React from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import CalendarEmbed from '../components/CalendarEmbed';
import VerticalNewsCarousel from '../components/VerticalNewsCarousel';
import { useNavigate } from 'react-router-dom';
import { useLanguage } from '../components/LanguageProvider';

function Home() {

  const {language} = useLanguage();

  const navigate = useNavigate();

  const handleLearnMore = () => {
    navigate('/research', { state: { smoothScroll: true } });
  };


  return (
  
    <>
    <Container className="my-5">
      <Row>
        <Col>
          <h1>{language !== 'fr' ? "Welcome to Phaedra Royle's research laboratory" : 'Bienvenue sur le laboratoire de recherche de Phaedra Royle'}</h1>
          <br />

          <p>{language !== 'fr' ? "Advancing knowledge in speech-language pathology." : "Pour faire avancer les connaissances en Orthophonie." }</p>
          <Button variant="primary" onClick={handleLearnMore}> {language !== 'fr' ? "Read more"  : "En savoir plus" }</Button>
          <img src={process.env.PUBLIC_URL + '/images/group.png'} alt="Research Lab" className="img-fluid" />
        </Col>
      </Row>
      
    </Container>

    <Container fluid className="my-5 calendar-section" style={{ backgroundColor: '#F1F2FA' }}>
      <Container className="my-5">
        <br />

        
        <h2 className="custom-margin-top"> {language !== 'fr' ? "Upcoming"  :"Évènements" } </h2>
        <h2>{language !== 'fr' ? "events"  :"à venir" }</h2>
        <Row className="flex-grow-1">
          <Col>
            <div className="calendar-wrapper">
              <CalendarEmbed/>
            </div>
          </Col>
        </Row>
      </Container>
    </Container>

    <Container fluid className="my-5 p-0">
      <Container className="my-5">
        <br />
        <h2 className="custom-margin-top"> {language !== 'fr' ? "Latest news "  :"Actualités" } </h2>
        <br />
        <VerticalNewsCarousel />
        


        </Container>

    </Container>

    </>

    
  );
}

export default Home;